.container {
  position: relative;
  width: 100%;
  display: flex;
  padding: 15px 30px;
  border: 0.5px solid #70707040;
}
.fon {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.56);
  backdrop-filter: blur(2px);
}
.wrapper {
  position: relative;
  padding-right: 10%;
  display: flex;
  width: 100%;
  justify-items: center;
  grid-gap: 5%;
  justify-content: space-between;
}
.back {
  position: absolute;
  width: 100%;
  height: 100%;
}
.wrapper-calendar {
  position: relative;
  display: flex;
  justify-content: space-around;
  width: 100%;
  grid-gap: 5%;
  padding-right: 10%;
}
.submitBlock {
  margin-left: auto;
  margin-right: 0;
  align-self: center;
}
@media (max-width: 1300px) {
  .wrapper {
    padding-right: 5%;
  }
}
