.container {
  position: relative;
  min-width: max-content;
  overflow: hidden;
}
.wrapper {
  position: relative;
  display: flex;
  width: max-content;
  align-items: center;
  grid-gap: 10px;
}
.wrapper img {
  position: relative;
  height: 25px;
}
.title {
  margin-bottom: 5px;
  opacity: 0.5;
  font-size: 80%;
  font-weight: 600;
}
.input {
  color: black;
  /*background: transparent;*/
  border: none;
  width: max-content;
  min-width: 100%;
  height: 50px;
  font-size: 100%;
  background: transparent;
}