.container {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  grid-gap: 10px;
  transition: 0.3s;
}
.bottom-long {
  transition: 0.3s;
  position: absolute;
  bottom: -5px;
  left: 50%;
  transform: translateX(-50%);
  height: 2px;
  border-radius: 50%;
  background: #e89732;
}
.container:hover .bottom-long {
  opacity: 0.5 !important;
  width: 130% !important;
}
.container:hover {
  opacity: 0.7;
}
.wrapper {
  position: relative;
  display: flex;
  align-items: center;
  grid-gap: 10px;
}
.wrapper img {
  position: relative;
  height: 23px;
  width: 23px;
}
.title {
  opacity: 0.5;
  font-size: 80%;
  font-weight: 600;
}
.data-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
}
.data-show-text {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  padding: 0;
  display: flex;
  align-items: center;
}
.data-show-text p{
  margin-top: 4px;
  font-weight: 500;
  font-size: 105%;
}